.dateanddate {
    font-size: small;
}

.projectlistStatus {
    color: #2379d5;
    font-weight: bold;
}


.Projectlisttable {
    margin: 20px 2px 10px 2px;
    background-color: #ffffff;
    box-shadow: 0px 0px 5px rgba(26, 112, 211, 0.3);
    color: black;
    border-radius: 20px;
    padding-bottom: 20px;
}



table {
    border-collapse: collapse;
    width: 100%;
}

table tr td {
    border-bottom: 1px solid #c8c8c8;
    border-spacing: 30px;
}

th {
    background-color: aliceblue;
}

td:nth-child(1),
th:nth-child(1) {
    padding-left: 20px;
}

td:last-child,
th:last-child {
    padding-right: 20px;
}

th,
td {
    text-align: left;
    padding: 8px 8px;
}

thead,
tbody {
    padding: 30px !important;
}



.pagination {
    display: flex;
    align-items: row;
    gap: 10px;
    justify-content: center;
}

.page-item {
    list-style: none;
    padding: 7px;
    border-radius: 5px;
    box-shadow: 0px 0px 5px rgba(26, 112, 211, 0.3);
}

.page-link {
    text-decoration: none;
}

.active {
    background-color: #2379d5;
    color: white;
}

td p {
    margin: 5px 0 5px 0;
}


.projectlistStatusbtn {
    background-color: white;
    color: #2379d5;
    border: 0.5px solid #2379d5;
    width: 55px;
    margin: 0;
    padding: 5px 0 5px 0;
}

.projectlistStatusbtn:hover {
    color: white;

}

.projectlisttop {
    display: flex;
    justify-content: space-between;
    padding: 30px 30px 10px 30px;
}

.ProjectlistSinput {
    border: none;
    border-bottom: 1px solid black;
    margin-bottom: 2px;
}

#SortBy {
    border: none;
}

.active-button {
    background-color: #2379d5;
    color: white;
}

.formobile {
    display: none;
}

@media only screen and (max-width: 800px) {

    .formobiledesign {
        background-color: white;
        margin-bottom: 20px;
        padding: 30px;
        border-radius: 15px;
        color: black;
    }

    .projtitalandstatus {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    .dateanddate {
        margin: 2px 0 0 0;
    }
    .namemob{
        margin: 0;
    }

    .statusbtnmob {
        margin-top: 20px;
        display: flex;
        justify-content: space-between;
    }

    .typemob {
        display: flex;
        align-items: center;
        padding: 10px 0 0 0;
    }
    .typemobi{
        margin: 0;
    }
        .reasonmob{
            padding: 20px 0 0 0;
            list-style: none;
        }

    .Locationmob,
    .Prioritymob {
        padding: 10px 0 0 0;
        list-style: none;
    }
    .Categorymob, .Deptmob{
        padding: 0 0 0 10px;
    }
    .makeitboldmob{
        font-weight: 500;
    }

    /* ----------------------- */
    .Projectlisttable {
        background: none;
        box-shadow: none;
        padding-top: 10px;
        margin-bottom: 0;
    }

    .ProjectlistSinput {
        background: none;

    }

    #SortBy {
        background: none;
    }


    .table {
        display: block;
        color: black;
    }


    .fordesktop {
        display: none;
    }

    .paginationNum {
        padding-top: -10px;
        padding-bottom: 30px;
    }

    .ProjectlistSinput {
        border: none !important;
        border-bottom: 1px solid black !important;
        margin-bottom: 2px !important;
        padding: 3px;
    }
}